.taxonomy-list {
    padding: 6rem 0;
    background-color: var(--color-background);

    .taxonomy-header {
        margin-bottom: 4rem;
        text-align: left;
        max-width: var(--container-width);
        margin-left: auto;
        margin-right: auto;
        padding: 0 1rem;

        h1 {
            margin-bottom: 1rem;
            font-size: 3.5rem;
            font-weight: 700;
            color: var(--color-text);
            line-height: 1.2;
        }

        p {
            font-size: 1.25rem;
            color: var(--color-text-light);
            max-width: 800px;
            line-height: 1.6;
        }
    }

    .posts-grid {
        display: grid;
        gap: 2rem;
        grid-template-columns: 1fr;
        max-width: var(--container-width);
        margin: 0 auto;
        padding: 0 1rem;

        @media (min-width: 768px) {
            grid-template-columns: repeat(2, 1fr);
            gap: 2.5rem;
        }

        @media (min-width: 1200px) {
            grid-template-columns: repeat(3, 1fr);
            gap: 3rem;
        }
    }

    .post-card {
        background: var(--color-card-background, rgba(255, 255, 255, 0.05));
        border-radius: 12px;
        overflow: hidden;
        transition: transform 0.3s ease;
        border: 1px solid rgba(255, 255, 255, 0.1);
        position: relative;
        cursor: pointer;
        text-decoration: none;
        color: inherit;
        display: block;

        &::after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            z-index: 1;
        }

        &:hover {
            transform: translateY(-4px);
        }

        article {
            display: flex;
            flex-direction: column;
            height: 100%;
            padding: 2rem;
        }

        header {
            margin-bottom: 1.5rem;

            h2 {
                margin: 0 0 1rem;
                font-size: 1.5rem;
                font-weight: 600;
                line-height: 1.3;
                color: var(--color-text);
            }

            time {
                display: block;
                font-size: 0.875rem;
                color: var(--color-text-light);
                opacity: 0.8;
            }
        }

        .post-description {
            flex-grow: 1;
            margin-bottom: 1.5rem;

            p {
                font-size: 1rem;
                line-height: 1.6;
                color: var(--color-text-light);
                margin: 0;
                opacity: 0.9;
            }
        }

        footer {
            margin-top: auto;

            .read-more {
                display: inline-flex;
                align-items: center;
                color: var(--color-primary);
                font-weight: 500;
                text-decoration: none;
                transition: color 0.2s ease;
                font-size: 0.875rem;
                text-transform: uppercase;
                letter-spacing: 0.05em;

                &:hover {
                    color: var(--color-primary-light);
                }
            }
        }
    }
} 