.supported-networks {
    position: relative;
    width: 100%;
    padding: 2rem 0;
    background-color: var(--color-background);
    overflow: hidden;
    
    &__title {
        text-align: center;
        font-size: 1.5rem;
        margin-bottom: 3rem;
        color: var(--color-text);
        font-weight: 500;
        opacity: 0.7;
    }

    &__scroll {
        display: flex;
        overflow: hidden;
        position: relative;
        width: 100%;
    }

    &__logos {
        display: flex;
        gap: 4rem;
        padding: 0 2rem;
        animation: slide 40s linear infinite;
        flex-shrink: 0;
        width: 100%;
    }

    &__logo {
        height: 2rem;
        width: auto;
        opacity: 0.7;
        transition: opacity 0.3s ease;
        filter: brightness(0) invert(1);
        flex-shrink: 0;
        
        &:hover {
            opacity: 1;
        }
        
        @media (min-width: 768px) {
            height: 2.5rem;
        }
    }
}

@keyframes slide {
    0% {
        transform: translateX(0);
    }
    100% {
        transform: translateX(-100%);
    }
}

// Gradient overlays
.supported-networks::before,
.supported-networks::after {
    content: '';
    position: absolute;
    top: 0;
    height: 100%;
    width: 100px;
    z-index: 2;
    pointer-events: none;
}

.supported-networks::before {
    left: 0;
    background: linear-gradient(90deg, var(--color-background), transparent);
}

.supported-networks::after {
    right: 0;
    background: linear-gradient(270deg, var(--color-background), transparent);
}