html {
  scroll-behavior: smooth;
  scroll-padding-top: 120px; // Increased padding for better spacing
}

// Header Component
.site-header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  background: var(--color-background-transparent);
  backdrop-filter: blur(12px) saturate(180%);
  -webkit-backdrop-filter: blur(12px) saturate(180%);
  border-bottom: 1px solid var(--color-border-light);

  &__container {
    max-width: 1440px;
    margin: 0 auto;
    padding: 0 2rem;

    @media (max-width: 1024px) {
      padding: 0 1.5rem;
    }
  }

  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 80px;
  }

  // Logo styles
  &__logo {
    a {
      text-decoration: none;
      color: var(--color-white);
    }

    .binary-title {
      font-family: 'Courier New', monospace;
      font-size: 1.5rem;
      font-weight: 700;
      letter-spacing: 0.05em;
      color: var(--color-white);
      margin: 0;
      
      &:hover {
        color: var(--color-text-muted);
        transition: color 0.3s ease;
      }

      @media (max-width: 1024px) {
        font-size: 1.25rem;
      }

      @media (max-width: 768px) {
        font-size: 1.125rem;
      }
    }
  }

  // Updated Navigation styles
  &__nav {
    flex: 1;
    margin: 0 2rem;

    &-list {
      display: flex;
      list-style: none;
      margin: 0;
      padding: 0;
      justify-content: center;
      gap: 2rem;
    }

    &-item {
      position: relative;

      &.dropdown {
        position: relative;
      }
    }

    &-button {
      color: var(--color-white);
      font-size: 1rem;
      padding: 0.75rem 1rem;
      cursor: pointer;
      display: flex;
      align-items: center;
      font-weight: 500;
      border-radius: 6px;
      transition: all 0.2s ease;
      text-decoration: none;

      &:hover {
        background: var(--color-background-alt);
        color: var(--color-white);
      }

      // Keep nav-icon only for non-dropdown buttons
      .nav-icon {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 20px;
        height: 20px;
        margin-right: 0.5rem;
        
        img {
          width: 16px;
          height: 16px;
          filter: invert(1);
        }
      }
    }
  }

  // Dropdown styles
  .dropdown {
    &-content {
      position: absolute;
      top: calc(100% + 1.5rem);
      left: -2rem;
      display: flex;
      background: var(--color-background);
      border-radius: 8px;
      opacity: 0;
      visibility: hidden;
      z-index: 1000;
      box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
      transition: opacity 0.3s ease, visibility 0.3s ease;
      overflow: visible;

      &::before {
        content: '';
        position: absolute;
        top: -1.5rem;
        left: 0;
        right: 0;
        height: 1.5rem;
      }
    }

    &-left {
      width: 280px;
      padding: 0.75rem;
      background: var(--color-background);
      border: 1px solid var(--color-border-light);
      border-radius: 8px;

      // When right panel is shown (handled by JS)
      &.has-right-panel {
        border-radius: 8px 0 0 8px;
        border-right: none;
      }
    }

    &-right {
      position: absolute;
      left: 279px;
      top: 0;
      width: 240px;
      height: 100%;
      padding: 0.75rem;
      background: #0d0624;
      border: 1px solid var(--color-border-light);
      border-radius: 0 8px 8px 0;
    }

    &-items {
      display: grid;
      gap: 0.5rem;
      padding: 0.25rem 0;
    }

    &-item {
      position: relative;
      display: flex;
      align-items: center;
      gap: 0.75rem;
      padding: 0.75rem;
      color: var(--color-white);
      text-decoration: none;
      border-radius: 6px;
      transition: background-color 0.2s ease;

      .item-icon {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 32px;
        height: 32px;
        background: rgba(255, 255, 255, 0.05);
        border-radius: 6px;
        transition: background-color 0.2s ease;

        img {
          width: 18px;
          height: 18px;
          filter: invert(1);
        }
      }

      .item-text {
        font-size: 0.95rem;
        font-weight: 500;
      }

      &:hover {
        background: rgba(255, 255, 255, 0.1);

        .item-icon {
          background: rgba(255, 255, 255, 0.1);
        }
      }
    }

    .submenu {
      display: none;
      opacity: 0;
      transition: opacity 0.2s ease;

      &-items {
        display: grid;
        gap: 0.25rem;
      }

      &-item {
        display: flex;
        align-items: center;
        padding: 0.75rem;
        color: var(--color-white);
        text-decoration: none;
        border-radius: 6px;
        font-size: 0.95rem;
        font-weight: 500;
        transition: background-color 0.2s ease;

        &:hover {
          background: rgba(255, 255, 255, 0.1);
        }
      }

      // When displayed by JavaScript
      &[style*="display: block"] {
        opacity: 1;
      }
    }

    // Show dropdown on hover
    &:hover {
      .dropdown-content {
        opacity: 1;
        visibility: visible;
      }
    }
  }

  &__actions {
    display: flex;
    align-items: center;
    gap: 1rem;

    &--desktop {
      @media (max-width: 1024px) {
        display: none;
      }
    }
  }

  &__mobile-actions {
    display: none;
    margin-top: auto;
    padding-top: 2rem;
    border-top: 1px solid var(--color-border-light);

    @media (max-width: 1024px) {
      display: flex;
      flex-direction: column;
      gap: 1.5rem;
      align-items: center;
      text-align: center;

      .site-header__social {
        justify-content: center;
      }
    }
  }

  &__social {
    display: flex;
    align-items: center;
    gap: 0.75rem;

    &-link {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 28px;
      height: 28px;
      background: rgba(255, 255, 255, 0.1);
      border-radius: 50%;
      transition: all 0.2s ease;

      &:hover {
        background: rgba(255, 255, 255, 0.2);
        transform: translateY(-1px);
      }

      .icon {
        width: 16px;
        height: 16px;
        filter: invert(1); // This makes black icons white
      }
    }
  }

  // Updated CTA styles
  &__cta {
    &-button {
      display: inline-block;
      padding: 0.75rem 2rem;
      background: transparent;
      color: var(--color-white);
      text-decoration: none;
      border-radius: 6px;
      font-weight: 500;
      font-size: 1rem;
      letter-spacing: 0.02em;
      transition: all 0.3s ease;
      border: 1.5px solid var(--color-border-light);

      &:hover {
        background: var(--color-background-alt);
        border-color: var(--color-white);
        transform: translateY(-1px);
      }
    }
  }

  &__mobile-toggle {
    display: none;
    background: none;
    border: none;
    padding: 0.5rem;
    margin-right: -0.5rem;
    cursor: pointer;
    position: relative;
    z-index: 1001;

    .hamburger-line {
      display: block;
      width: 24px;
      height: 2px;
      background-color: var(--color-white);
      transition: transform 0.3s ease, opacity 0.3s ease;
      
      & + .hamburger-line {
        margin-top: 6px;
      }
    }

    &.is-active {
      .hamburger-line {
        &:nth-child(1) {
          transform: translateY(8px) rotate(45deg);
        }
        &:nth-child(2) {
          opacity: 0;
        }
        &:nth-child(3) {
          transform: translateY(-8px) rotate(-45deg);
        }
      }
    }
  }

  // Mobile styles
  @media (max-width: 1024px) {
    &__logo {
      margin-left: -0.5rem;
    }

    &__mobile-toggle {
      display: block;
    }

    &__nav {
      position: fixed;
      top: 0;
      right: -100%;
      width: 100%;
      max-width: 400px;
      height: 100vh;
      background: var(--color-background);
      border-left: 1px solid var(--color-border-light);
      padding: 5rem 1.5rem 2rem;
      overflow-y: auto;
      transition: right 0.3s ease;
      margin: 0;
      display: flex;
      flex-direction: column;

      &.is-active {
        right: 0;
      }

      &-list {
        flex-direction: column;
        gap: 0.5rem;
      }

      &-item {
        width: 100%;

        &.dropdown {
          .dropdown-content {
            position: static;
            opacity: 1;
            visibility: visible;
            box-shadow: none;
            padding: 0.5rem 0 0.5rem 1rem;
            display: none;
            transition: none;

            &.is-active {
              display: block;
            }
          }
        }
      }

      &-button {
        width: 100%;
        justify-content: space-between;
        
        &.dropdown-trigger:after {
          content: '+';
          font-size: 1.2rem;
          opacity: 0.5;
        }

        &.is-active:after {
          content: '-';
        }
      }
    }

    .dropdown {
      &-content {
        flex-direction: column;
        border: none;
        padding: 0;
        overflow: hidden;
        transition: none;
      }

      &-left, &-right {
        width: 100%;
        position: static;
        border: none;
        padding: 0;
      }

      &-items {
        padding: 0;
      }

      &-item {
        padding: 0.75rem 1rem;
        justify-content: flex-start;

        &.has-submenu {
          &:after {
            display: none;
          }
        }
      }

      .submenu {
        padding-left: 0;
        display: none;
        transition: none;
        
        &.is-active {
          display: block;
        }
        
        &-items {
          padding: 0;
        }

        &-item {
          padding: 0.75rem 1rem;
          justify-content: flex-start;
        }
      }
    }

    &__actions {
      margin-top: 2rem;
      flex-direction: column;
      align-items: flex-start;
      gap: 1.5rem;
    }

    &__social {
      width: 100%;
      justify-content: flex-start;
    }

    &__cta {
      width: 100%;
      
      &-button {
        width: 100%;
        text-align: center;
      }
    }
  }

  // Remove the conflicting mobile styles
  @media (max-width: 768px) {
    &__wrapper {
      justify-content: space-between;
    }

    &__nav {
      margin: 0; // Remove the margin that might affect positioning
    }

    &__actions {
      gap: 1rem;
    }
  }
} 