@keyframes slideOutUp {
  0% {
    transform: translateY(0);
    opacity: 1;
  }
  100% {
    transform: translateY(-100%);
    opacity: 0;
  }
}

@keyframes slideInUp {
  0% {
    transform: translateY(100%);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes gradient-shift {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@keyframes pattern-shift {
  0% {
    background-position: 0 0, 0 10px, 10px -10px, -10px 0px;
  }
  100% {
    background-position: 20px 20px, 20px 30px, 30px 10px, 10px 20px;
  }
}

.hero {
  min-height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  background-color: var(--color-background);
  color: var(--color-text);
  position: relative;
  overflow: hidden;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-image: 
      linear-gradient(45deg, var(--color-background) 25%, transparent 25%),
      linear-gradient(-45deg, var(--color-background) 25%, transparent 25%),
      linear-gradient(45deg, transparent 75%, var(--color-background) 75%),
      linear-gradient(-45deg, transparent 75%, var(--color-background) 75%);
    background-size: 20px 20px;
    background-position: 0 0, 0 10px, 10px -10px, -10px 0px;
    opacity: 0.05;
    animation: pattern-shift 20s linear infinite;
  }

  &__container {
    max-width: 800px;
    margin: 0 auto;
    text-align: center;
    position: relative;
    z-index: 1;
    padding-top: clamp(2rem, 5vh, 4rem);
  }

  &__title {
    font-size: 4rem;
    font-weight: 700;
    margin-bottom: 1.5rem;
    line-height: 1.2;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0;
  }

  &__title-line {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
    height: 1.2em;
    width: 100%;
  }

  &__static-text {
    display: inline-block;
    line-height: 1.2;
  }

  &__building {
    font-size: 1em;
    line-height: 1.2;
    font-weight: 700;
  }

  &__dynamic-wrapper {
    height: 1.2em;
    overflow: hidden;
    position: relative;
    min-width: 320px;
    padding: 0 5px;
  }

  &__dynamic-container {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
  }

  &__dynamic-text {
    position: absolute;
    width: 100%;
    height: 100%;
    text-align: left;
    backface-visibility: hidden;
    will-change: transform, opacity;
    line-height: 1.2;
    font-weight: 800;
    font-size: 1em;
    display: flex;
    align-items: center;
    white-space: nowrap;
    justify-content: flex-start;
    background: linear-gradient(
      135deg,
      #FF6B6B 0%,
      #FF758C 15%,
      #4ECDC4 30%,
      #6EE7B7 45%,
      #96C93D 60%,
      #A7D129 75%,
      #FF6B6B 90%
    );
    background-size: 200% 200%;
    animation: gradient-shift 15s ease-in-out infinite;
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    filter: brightness(1.2) contrast(1.1);
  }

  &__subtitle {
    font-size: 1.1rem;
    max-width: 600px;
    margin: 0 auto 3rem;
    opacity: 0.9;
    text-align: center;
    line-height: 1.6;
  }

  &__cta {
    display: flex;
    gap: 1rem;
    justify-content: center;
  }
}

.button {
  padding: 0.875rem 1.75rem;
  border-radius: 0.375rem;
  font-weight: 600;
  text-decoration: none;
  border: none;
  display: flex;
  align-items: center;
  gap: 0.5em;
  font-size: 1rem;
  transition: all 0.2s ease;

  &:hover {
    transform: translateY(-1px);
  }

  &__arrow {
    transition: transform 0.2s ease;
    font-size: 1.1em;
    line-height: 0;
    position: relative;
    top: 1px;
  }

  &:hover &__arrow {
    transform: translateX(4px);
  }

  &--primary {
    background-color: #FFFFFF;
    color: #000000;
  }

  &--secondary {
    background-color: transparent;
    color: #FFFFFF;
  }
}

@media (min-width: 769px) and (max-width: 1024px) {
  .hero {
    &__title-line {
      flex-direction: column;
      gap: 15px;
      height: auto;
    }

    &__dynamic-wrapper {
      min-width: 300px;
    }

    &__dynamic-text {
      justify-content: center;
      text-align: center;
    }
  }
}

@media (max-width: 768px) {
  .hero {
    &__title {
      font-size: 2.5rem;
    }

    &__title-line {
      flex-direction: column;
      gap: 10px;
      height: auto;
    }

    &__dynamic-wrapper {
      min-width: 280px;
    }

    &__dynamic-text {
      justify-content: center;
      text-align: center;
    }

    &__subtitle {
      font-size: 0.95rem;
      max-width: 450px;
      margin-bottom: 2.5rem;
    }
  }

  .button {
    padding: 0.75rem 1.5rem;
    font-size: 0.9rem;
  }
} 