.footer {
  padding: 4rem 2rem;
  background-color: var(--color-background);
  color: var(--color-text);
  
  &__container {
    max-width: 1200px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 2rem;
    
    @media (min-width: 768px) {
      grid-template-columns: repeat(3, 1fr);
    }
  }
  
  &__column {
    margin-bottom: 2rem;
    
    @media (min-width: 768px) {
      margin-bottom: 0;
    }
  }
  
  &__heading {
    font-size: 1.125rem;
    font-weight: 600;
    margin-bottom: 1rem;
    color: var(--color-text-lighter);
  }
  
  &__list {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  &__item {
    margin-bottom: 0.5rem;
    
    &:last-child {
      margin-bottom: 0;
    }
  }
  
  &__link {
    color: var(--color-text-muted);
    text-decoration: none;
    transition: color 0.2s ease;
    
    &:hover {
      color: var(--color-primary);
    }
  }
  
  &__address {
    font-style: normal;
    line-height: 1.5;
    color: var(--color-text-muted);
  }
  
  &__legal-text {
    margin-top: 1rem;
    font-size: 0.875rem;
    color: var(--color-text-muted);
  }
  
  &__bottom {
    max-width: 1200px;
    margin: 2rem auto 0;
    padding-top: 2rem;
    border-top: 1px solid var(--color-border-light);
  }
  
  &__copyright {
    font-size: 0.875rem;
    text-align: center;
    color: var(--color-text-muted);
  }
} 