.section--contact {
  min-height: calc(100vh - 80px);
  padding-top: 80px;
  background-color: var(--color-background);
}

.contact-page {
  width: 100%;
  padding: 2rem 0;
  text-align: center;

  &__container {
    max-width: 680px;
    margin: 0 auto;
    padding: 0 1.5rem;
  }

  &__title {
    font-size: 1.75rem;
    font-weight: 700;
    margin-bottom: 0.75rem;
    color: var(--color-white);
    line-height: 1.2;
  }
}

.contact-content {
  margin-bottom: 1.5rem;
  font-size: 0.875rem;
  line-height: 1.5;
  color: var(--color-text-light);

  p {
    margin-bottom: 0.5rem;
  }
}

.contact-form {
  background: var(--color-surface);
  padding: 1.75rem;
  border-radius: 12px;
  border: 1px solid var(--color-border-light);
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  text-align: left;

  .form-row {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
    margin-bottom: 1rem;

    @media (max-width: 640px) {
      grid-template-columns: 1fr;
      gap: 0.75rem;
    }
  }

  .form-group {
    margin-bottom: 1rem;

    &:last-of-type {
      margin-bottom: 1.5rem;
    }
  }

  label {
    display: block;
    margin-bottom: 0.5rem;
    font-weight: 500;
    color: var(--color-text);
    font-size: 0.875rem;

    .required {
      color: var(--color-coral);
      margin-left: 0.25rem;
    }
  }

  input,
  textarea {
    width: 100%;
    padding: 0.75rem;
    background: var(--color-white);
    border: 1px solid var(--color-border-light);
    border-radius: 6px;
    color: var(--color-black);
    font-family: inherit;
    font-size: 0.875rem;
    transition: all 0.2s ease;
    
    &:focus {
      outline: none;
      border-color: var(--color-primary);
      box-shadow: 0 0 0 2px rgba(123, 77, 255, 0.15);
    }

    &::placeholder {
      color: var(--color-text-muted);
    }
  }

  textarea {
    resize: vertical;
    min-height: 100px;
  }

  .submit-button {
    display: block;
    margin: 0 auto;
    padding: 0.75rem 2rem;
    background: var(--color-primary);
    color: var(--color-white);
    border: none;
    border-radius: 6px;
    font-size: 0.875rem;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.2s ease;
    min-width: 140px;

    &:hover {
      background: var(--color-primary-dark);
      transform: translateY(-1px);
    }

    &:active {
      transform: translateY(0);
    }
  }
}

// Responsive styles
@media (max-width: 768px) {
  .contact-page {
    padding: 1.5rem 0;

    &__title {
      font-size: 1.5rem;
    }
  }

  .contact-form {
    padding: 1.25rem;
  }
} 